import Head from "next/head";
import { useRouter } from "next/router";

import moment from "moment";

import withLayout from "../../components/Layout";
// import posts from "";
import styles from "./styles.scss";

const Post = ({ className, post }) => {
  const router = useRouter();
  if (!post)
    return (
      <p
        style={{ textAlign: "center", fontWeight: "bold" }}
        className={className}
      >
        Упс... Такого посту ще не існує{" "}
        <button
          onClick={() => router.back()}
          style={{
            display: "block",
            margin: "10px auto",
            border: "none",
            background: "none",
            color: "#19b5fe",
            fontSize: "16px",
          }}
        >
          Вернутися назад
        </button>
      </p>
    );

  return (
    <div className={className}>
      <Head>
        <title>ФКЕП - {post.title}</title>
        <meta name="viewport" content="initial-scale=1.0, width=device-width" />
      </Head>
      {post.photo ? (
        <img src={post.photo} className={styles.photo} alt={post.title} />
      ) : undefined}
      <h2 className={styles.title}>{post.title}</h2>
      <p className={styles.text}>{post.text}</p>
      <p className={styles.text}>{post.paragraph}</p>
      <p className={styles.text}>{post.end}</p>
      <span className={styles.date}>
        {moment(post.date).locale("uk").fromNow()}
      </span>
    </div>
  );
};

export async function getStaticPaths() {
  const posts = await (await import("../../api/news.json")).default;
  const slugs = posts.map((post) => post.id);
  const paths = slugs.map((slug) => ({ params: { slug } }));
  return {
    paths,
    fallback: false,
  };
}

export async function getStaticProps({ params: { slug } }) {
  const posts = await (await import("../../api/news.json")).default;
  const post = posts.find((x) => x.slug === slug);
  return {
    props,
  };
}

export default withLayout(Post);
